  transition: ease background-color 0.3s;
}
.point:hover {
  background-color: #85ad48;
}
.point:after {
  content: "";
  position: absolute;
  left: 5px;
  right: 5px;
  border-bottom: 2px solid #ffffff;
  top: calc(50% - 1px);
}
.point:before {
  content: "";
  position: absolute;
  top: 5px;
  bottom: 5px;
  border-left: 2px solid #ffffff;
  left: calc(50% - 1px);
}
